<template>
  <!-- Maplibre map | Sidebar | Created by Bayram Gurel-->
  <div id="map" class="position-absolute w-100 h-100">
    <div id="sidebar" v-bind:class="{ collapsed: isCollapsed }" @click="toggleSidebar" class="d-flex align-items-center justify-content-center position-absolute end-0">
      <div class="position-absolute start-neg btn btn-light btn-lg d-flex align-items-center justify-content-center sidebar-toggle">
        ←
      </div>
      <div id="sidebar-content" class="rounded bg-white shadow p-3 w-100 h-100">
        <div class="col">
          <h3>Informatie over de stations</h3>
        </div>
        <div class="col">
          <h6>Laatst gemeten uren waarde van <b class="text-primary">concentration {{ property }} nk (µg/m³)</b> van vandaag.</h6>
        </div>
        <div class="row table-responsive" style="max-height: 33vh; overflow-y: auto;">
          <table class="table table-bordered">
            <thead>
            <tr>
              <th scope="col">Station</th>
              <th scope="col">Gemiddelde</th>
              <th scope="col">maximum</th>
              <th scope="col">Minimaal</th>
            </tr>
            </thead>
            <tbody class="table-group-divider">
            <tr v-for="item in geojson.features" :key="item.properties.station_name">
              <th scope="row">{{ item.properties.station_name }}</th>
              <td>{{ item.properties.avg_value }}</td>
              <td>{{ item.properties.max_value }}</td>
              <td>{{ item.properties.min_value }}</td>
            </tr>
            </tbody>
          </table>
        </div>
        <p class="py-3">Laatst gemeten uren waardes op een dag</p>
        <div class="row pt-3">
          <h3>Beschrijving van fijnstof | <span class="property text-primary">{{ property }}</span></h3>
          <br>
          <h6 class="description" v-html="description" style="max-height: 33vh; overflow-y: auto;"></h6>
        </div>
        <div class="row">
          <h5 class="pt-4"><a href="https://samenmeten.rivm.nl/dataportaal/" target="_blank" class="link-primary link-offset-2 link-underline-opacity-25 link-underline-opacity-100-hover">Bekijk dit filmpje voor meer informatie:</a></h5>
          <p><a href="https://www.samenmeten.nl/zelf-meten/hoe-kan-ik-zelf-luchtkwaliteit-meten" target="_blank" class="link-primary link-offset-2 link-underline-opacity-25 link-underline-opacity-100-hover">Hoe kan ik zelf luchtkwaliteit meten<i class="bi bi-question-lg"></i></a></p>
          <video controls="" height="200vh">
            <source src="https://www.rovid.nl/rivm/aco/2017/rivm-aco-20171017-id2nv5d79-web-hd.mp4" type="video/mp4" />
            Your browser does not support HTML5 video.
          </video>
        </div>
      </div>
    </div>
    <!--<div class="position-absolute w-100 h-100" style="z-index: 1;"></div>   -->
  </div>
  <!--  Above the map  -->
  <div class="container-fluid">
    <div class="position-absolute top-0 start-0 shadow m-3 col-md-4 col-6">
      <!--  The logo | Hour slider | upload GeoJSON file  -->
      <div class="container bg-white rounded pb-3">
        <div class="row">
          <div class="col-12">
            <img :src="require('@/assets/PZH-logo.png')" class="img-fluid rounded mx-auto d-block" title="Refresh website" @click="reloadPage">
            <h4 class="text-center">Provincie Zuid Holland - Luchtkwaliteit dashboard</h4>
          </div>
        </div>
        <div class="row px-3">
          <div class="col-12">
            <label for="timeSlider">Geselecteerd uur: {{ timeValue }}:00</label>
            <input id="timeSlider" ref="timeSlider" class="form-control-range w-100" type="range" min="0" max="23" step="1" v-model="timeValue" @change="updateLayer" @mousedown="stopSlider">
          </div>
        </div>
        <div class="row pt-2 px-3">
          <div class="col">
            <div class="form-floating mb-2 focused-label">
              <input type="text" list="dayNames" v-model="selectedDay" class="form-control text-primary rounded shadow-sm" @input="updateLayer" @click="clearInput('sDate')" id="floatingInput" placeholder=" " ref="sDate">              <label for="floatingInput">Selecteer een dag</label>
            </div>
            <datalist id="dayNames">
              <option v-for="(dayName, index) in dayNames" :value="dayName" :key="index">{{ dayName }}</option>
            </datalist>
          </div>
          <div class="col text-center">
            <button id="playSlider" type="button" class="col-10 btn btn-outline-primary py-2" @click="startSlider">
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-play-circle-fill" viewBox="0 0 16 16">
                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM6.79 5.093A.5.5 0 0 0 6 5.5v5a.5.5 0 0 0 .79.407l3.5-2.5a.5.5 0 0 0 0-.814l-3.5-2.5z"></path>
              </svg>
              Afspelen
            </button>
          </div>
        </div>
        <div class="row">

          <div class="col pt-2"><p class="text-end mb-0" v-text="isFrom"></p></div>
        </div>
      </div>
      <div class="bg-white rounded p-2 my-2">
        <nav>
          <div class="nav nav-tabs" role="tablist">
            <button class="nav-link active" id="nav-leganda-tab" data-bs-toggle="tab" data-bs-target="#nav-leganda" type="button" role="tab" aria-controls="nav-leganda" aria-selected="true">Gegevens legandaen</button>
            <button class="nav-link" id="nav-wms-tab" data-bs-toggle="tab" data-bs-target="#nav-wms" type="button" role="tab" aria-controls="nav-wms" aria-selected="false">IDW interpolatie</button>
          </div>
        </nav>
        <div class="tab-content">
          <div class="tab-pane fade show active" id="nav-leganda" role="tabpanel" aria-labelledby="nav-leganda-tab" tabindex="0">
            <h3 class="my-2 text-center">Legenda | Filter <i class="bi bi-funnel text-primary"></i></h3>
            <select ref="property" class="btn mb-1 form-select form-select-lg mb-3 btn-outline-primary text-primary rounded-2 shadow-sm" @input="updateLayer" style="background: white !important;">
              <option value="no2">SensorData - Stikstofdioxide | N02</option>
              <option value="pm10">SensorData - Fijnstof 10microm | pm10</option>
              <option selected value="pm25">SensorData - Fijnstof 2.5microm | pm25</option>
            </select>
            <p class="text-center">Concentration {{ property }} nk (µg/m³)</p>
            <!--                <div class="bar rounded">&nbsp;</div>-->
            <div class="row px-2 text-center text-light">
              <div class="col" v-for="(value, index) in legendaValues" :key="index">
                <div class="rounded" :style="{ backgroundColor: colors[index] }"><span class='Legenda'>{{ value }}</span></div>
              </div>
            </div>
            <div class="row pt-1">
              <h6 class="col"><small class="text-muted">{{ concentrationValues[0] }} Laag concentratie</small></h6>
              <h6 class="col text-end"><small class="text-muted">Hoog concentratie {{ concentrationValues[1] }}</small></h6>
            </div>
          </div>
          <div class="tab-pane fade" id="nav-wms" role="tabpanel" aria-labelledby="nav-wms-tab" tabindex="0">
            <div class="row p-2">
              <div class="col text-center">
                <h3 class="pb-2 text-primary border-bottom border-primary">IDW interpolatie</h3>
                <div class="row my-4">
                  <div class="col">
                    <div class="btn-group btn-group-sm" role="group" aria-label="Basic radio toggle button group">
                      <input type="radio" class="btn-check" name="btnradio" value="disable" id="disable" autocomplete="off" v-model="interpolationStatus" @change="updateLayer">
                      <label class="btn btn-outline-danger" for="disable">Disable</label>

                      <input type="radio" class="btn-check" name="btnradio" value="activate" id="activate" autocomplete="off" v-model="interpolationStatus" @change="updateLayer">
                      <label class="btn btn-outline-primary" for="activate">Activate</label>
                    </div>
                  </div>
                  <img :src="require('@/assets/interpolatie-legenda.png')" class="img-fluid rounded w-auto h-auto">
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- filter -->
      <div class="bg-white rounded p-2">
        <nav>
          <div class="nav nav-tabs" role="tablist">
            <button class="nav-link active" id="nav-filter-tab" data-bs-toggle="tab" data-bs-target="#nav-filter" type="button" role="tab" aria-controls="nav-filter" aria-selected="true">Gegevens filteren</button>
            <button class="nav-link" id="nav-bestanden-tab" data-bs-toggle="tab" data-bs-target="#nav-bestanden" type="button" role="tab" aria-controls="nav-bestanden" aria-selected="false">Bestandenbeheer</button>
            <button class="nav-link" id="nav-contact-tab" data-bs-toggle="tab" data-bs-target="#nav-contact" type="button" role="tab" aria-controls="nav-contact" aria-selected="false">Contactgegevens</button>
          </div>
        </nav>
        <div class="tab-content">
          <div class="tab-pane fade show active" id="nav-filter" role="tabpanel" aria-labelledby="nav-filter-tab" tabindex="0">
            <h3 class="py-2 text-center text-secondary-emphasis border-bottom border-secondary-subtle">Filteren van stations</h3>
            <div class="row p-2">
              <div class="accordion accordion-flush" id="accordionExample">
                <div class="accordion-item">
                  <h2 class="accordion-header" id="headingOne">
                    <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                      Selecteer een Regio
                    </button>
                  </h2>
                  <div id="collapseOne" class="accordion-collapse collapse" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
                    <div class="accordion-body" style="max-height: 200px; overflow-y: auto;">
                      <div v-for="checkbox in regio" :key="checkbox.id" class="form-check">
                        <input type="checkbox" :id="checkbox.id" :value="checkbox.id" :name="'regio'" v-model="checkbox.checked" @input="updateLayer" class="form-check-input">
                        <label :for="checkbox.id" class="form-check-label">{{ checkbox.label }}</label>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="accordion-item">
                  <h2 class="accordion-header" id="headingTwo">
                    <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                      Selecteer een Gemeente
                    </button>
                  </h2>
                  <div id="collapseTwo" class="accordion-collapse collapse" aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
                    <div class="accordion-body" style="max-height: 200px; overflow-y: auto;">
                      <div v-for="checkbox in Gemeente" :key="checkbox.id" class="form-check">
                        <input type="checkbox" :id="checkbox.id" :value="checkbox.id" :name="'Gemeente'" v-model="checkbox.checked" @input="updateLayer" class="form-check-input">
                        <label :for="checkbox.id" class="form-check-label">{{ checkbox.label }}</label>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="accordion-item">
                  <h2 class="accordion-header" id="headingThree">
                    <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                      Selecteer een Station
                    </button>
                  </h2>
                  <div id="collapseThree" class="accordion-collapse collapse" aria-labelledby="headingThree" data-bs-parent="#accordionExample">
                    <div class="accordion-body" style="max-height: 200px; overflow-y: auto;">
                      <div class="form-floating mb-2 focused-label">
                        <input type="text" v-model="search" ref="searchInput" class="form-control text-primary rounded shadow-sm" @input="selectMatchingStations" list="stations" id="floatingInput" placeholder=" ">
                        <label for="floatingInput">Zoeken naar een station</label>
                      </div>
                      <datalist id="stations">
                        <option v-for="checkbox in station_name" :key="checkbox.id" :value="checkbox.label"></option>
                      </datalist>
                      <div v-for="checkbox in station_name" :key="checkbox.id" class="form-check">
                        <input type="checkbox" :id="checkbox.id" :value="checkbox.id" :name="'station_name'" v-model="checkbox.checked" @input="updateLayer" class="form-check-input">
                        <label :for="checkbox.id" class="form-check-label">{{ checkbox.label }}</label>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="tab-pane fade" id="nav-bestanden" role="tabpanel" aria-labelledby="nav-bestanden-tab" tabindex="0">
            <div class="row p-2">
              <div class="col text-center">
                <h3 class="pb-2 text-primary border-bottom border-primary">Bestandenbeheer</h3>
                <div class="row my-4">
                  <div class="col-4 border-end border-primary-subtle">
                    <div class="custom-container">
                      <img :src="require('@/assets/doc-logo.png')" class="img-fluid rounded custom-image">
                      <div class="custom-overlay rounded" @click="handleClick">
                        <div class="custom-text">Gegevens downloaden van CSV en GeoJSON.</div>
                      </div>
                    </div>
                  </div>
                  <div class="col">
                    <h5>Upload GeoJSON-bestand!</h5>
                    <p>De afbeeldingen resulteren in een CSV- en GeoJSON-bestand van de bijbehorende gegevens.</p>
                    <div class="input-group">
                      <input type="file" class="form-control text-primary rounded shadow-sm" id="inputGroupFile04" aria-describedby="inputGroupFileAddon04" aria-label="Upload" ref="localFile" @input="updateLayer">
                      <button class="btn btn-outline-danger" type="button" @click="clearInput('localFile')">
                        <i class="bi bi-x-lg"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="tab-pane fade" id="nav-contact" role="tabpanel" aria-labelledby="nav-contact-tab" tabindex="0">
            <div class="row p-2">
              <div class="text-center">
                <h3 class="pb-2 text-success border-bottom border-success">Contactgegevens</h3>
                <p class="my-4">Team Geo | <span class="text-success">teamgeo@pzh.nl</span><br>Bayram Gurel | b.gurel@pzh.nl</p>
              </div>
              <div class="border-top border-success-subtle rounded">
                <p><b class="text-success">Postadres</b></p>
                <p class="ps-3">
                  Provincie Zuid-Holland<br>Contact Centrum - Postbus 90602 - 2509 LP Den Haag<br><br>
                  Telefonische bereikbaarheid<br>Tijdens kantooruren (08:30 - 17:00 uur): 070 441 6611
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="offcanvas offcanvas-end" data-bs-scroll="true" tabindex="-1" id="offcanvasWithBothOptions" aria-labelledby="offcanvasWithBothOptionsLabel" style="width: 80vh">
      <div class="offcanvas-header">
        <h3 class="offcanvas-title" id="offcanvasWithBothOptionsLabel">Informatie over de station: <span v-text="properties.station_name"></span></h3>
        <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
      </div>
      <div class="offcanvas-body">
        <i>Het station bevindt zich in de gemeente {{ properties.Gemeente }}.<br>
          De fijnstofwaarde {{ properties.property }} is gemeten op dit station, dat zich in de regio
          {{ properties.regio }} bevindt.<br></i>
        <h4 class="text-center pt-4">Grafische representatie van {{ properties.station_name }}</h4>
        <canvas ref="myChart" width="30vh" height="30vh"></canvas>
      </div>
    </div>
    <div class="toast-container position-fixed bottom-0 end-0 p-3" style="z-index: 9999;">
      <div ref="liveToast" class="toast align-items-center text-white bg-primary border-0" role="alert" aria-live="assertive" aria-atomic="true">
        <div class="d-flex">
          <div class="toast-body">
            <i class="bi bi-patch-check me-2"></i>
            De bestanden zijn gedownload.
            <small class="d-block mt-1">{{ timeString }}</small>
          </div>
          <button type="button" class="btn-close btn-close-white me-2 m-auto" data-bs-dismiss="toast" aria-label="Close"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Created by Bayram Gurel
import { data } from '@/data/variable.js';

export default {
  name: 'SamenMetenDashboard',
  data() {
    return data;
  },

  computed: {
    dayNames() {
      return Array.from({length: 30}, (_, i) => {
        const date = new Date();
        date.setDate(date.getDate() - i);
        return this.formatDate(date);
      });
    },
  },

  watch: {
    selectedDay(newVal) {
      if (this.dayNames.includes(newVal)) {
        this.updateLayer();
      }
    },
  },

  created() {
    this.STYLE_URL = `https://api.maptiler.com/maps/outdoor/style.json?key=${this.API_KEY}`;
    this.checkHourChange();
  },

  async mounted() {
    this.initializeMap();
    this.addControls();
    this.elements = this.ids.reduce((acc, id) => {
      acc[id] = this.$refs[id];
      return acc;
    }, {});

    this.geojson = await this.fetchData('https://dta-samenmeten-api.azurewebsites.net/api/data/stations');

    [this.regios, this.gemeentes, this.stName] = await Promise.all([
      this.getUniqueItems(this.geojson, 'regio'),
      this.getUniqueItems(this.geojson, 'Gemeente'),
      this.getUniqueItems(this.geojson, 'station_name')
    ]);

    this.createCheckboxes('regio', this.regios);
    this.createCheckboxes('Gemeente', this.gemeentes);
    this.createCheckboxes('station_name', this.stName);
    this.selectedDay = this.dayNames[0];
    this.updateLayer()
  },

  methods: {
    async initializeMap() {
      this.map = new window.maplibregl.Map({
        container: 'map',
        style: this.STYLE_URL,
        center: [4.258788, 52.008663],
        zoom: 9,
      });
    },

    async addControls() {
      this.map.addControl(new window.maplibregl.FullscreenControl());
      this.map.addControl(new window.maplibregl.NavigationControl());
      this.map.addControl(new window.maplibregl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        trackUserLocation: true
      }));
      this.addStyleSwitchControl();
    },

    addStyleSwitchControl() {
      const styleSwitcher = this.createStyleSwitcher();
      const styleSwitchControl = this.createStyleSwitchControl(styleSwitcher);

      this.map.addControl(styleSwitchControl);
    },

    createStyleSwitcher() {
      const styleSwitcher = document.createElement('select');
      styleSwitcher.className = 'maplibregl-ctrl maplibregl-ctrl-group';

      const styles = this.STYLE_URLS.map(url => `${url}${this.API_KEY}`);

      styles.forEach((style, index) => {
        const option = document.createElement('option');
        option.value = style;
        option.text = this.STYLE_NAMES[index] || `Style ${index + 1}`;
        styleSwitcher.appendChild(option);
      });

      styleSwitcher.onchange = async (event) => {
        try {
          this.map.setStyle(event.target.value);
          setTimeout(() => {
            this.updateLayer();
          }, 50);
        } catch (error) {
          console.error('An error occurred while switching styles:', error);
        }
      };

      return styleSwitcher;
    },

    createStyleSwitchControl(styleSwitcher) {
      return {
        onAdd: () => {
          return styleSwitcher;
        },
        onRemove: function () {
        },
        getDefaultPosition: function () {
          return 'top-right';
        },
      };
    },


    async reloadPage() {
      window.location.reload();
    },
    clearInput(refName) {
      this.$refs[refName].value = '';
      if (refName === 'sDate') {
        this.selectedDay = '';
      } else {
        this.updateLayer()
      }
    },
    async startSlider() {
      if (this.interval) clearInterval(this.interval);
      this.timeValue = 0;
      this.updateLayer();
      this.interval = setInterval(() => {
        if (this.timeValue < new Date().getHours()) {
          this.timeValue++;
        } else {
          clearInterval(this.interval);
          this.interval = null;
        }
        this.updateLayer();
      }, 1750);
    },

    async stopSlider() {
      if (this.interval) {
        clearInterval(this.interval);
        this.interval = null;
      }
    },

    async selectMatchingStations() {
      const lowerCaseSearch = this.search.toLowerCase();
      this.station_name.forEach(station => {
        station.checked = station.label.toLowerCase() === lowerCaseSearch;
      });
      this.updateLayer();
    },

    async toggleSidebar() {
      this.isCollapsed = !this.isCollapsed;
      const padding = {sidebar: this.isCollapsed ? 0 : 300};
      this.map.easeTo({padding, duration: 800});
    },


    async fetchData(url) {
      let response = await fetch(url, {method: "GET", credentials: "include", keepalive: true});

      return await response.json();
    },

    async loadLocalFile() {
      try {
        let geojson = JSON.parse(await this.elements.localFile.files[0].text());
        geojson.Features = geojson.features;
        delete geojson.features;
        return geojson;
      } catch (error) {
        console.error('Error reading local file:', error);
        throw error;
      }
    },


    async getUniqueItems(geojson, uniqueColumn) {
      return [...new Set(geojson.Features.map(Feature => Feature.properties?.[uniqueColumn]))];
    },

    async createCheckboxes(id, items) {
      const selectedItems = this[id] ? this[id].filter(i => i.checked).map(i => i.id) : [];
      this[id] = items.sort().map(item => ({
        id: item,
        label: item,
        checked: selectedItems.includes(item)
      }));
    },

    async getSelectedValues(name) {
      return Array.from(document.querySelectorAll(`input[name="${name}"]:checked`)).map(input => input.value);
    },

    async checkHourChange() {
      setInterval(async () => {
        this.updateLayer();
      }, 1000 * 60 * 30);
    },

    async updateLayer() {
      const prop = this.propValues[this.elements.property.value] || this.propValues.default;
      this.property = prop.property;
      this.description = prop.description;
      this.legendaValues = prop.legendaValues;
      this.concentrationValues = prop.concentrationValues;

      const selectedRegio = await this.getSelectedValues('regio');
      const selectedGemeente = await this.getSelectedValues('Gemeente');
      const selectedStName = await this.getSelectedValues('station_name');
      this.reloadLayer(this.map, this.elements.timeSlider.value, selectedRegio, selectedGemeente, selectedStName).catch(error => console.error('Error reloading layer:', error));
    },

    formatDate(date) {
      const dayName = this.days[date.getDay()];
      const dateString = date.toLocaleDateString('nl-NL', {day: 'numeric', month: 'long', year: 'numeric'});
      return `${dayName} | ${dateString}`;
    },

    async reloadLayer(map, hour, selectedRegio, selectedGemeente, selectedStName) {
      try {
        const {files: localFiles = []} = this.$refs.localFile || {};
        this.isLocalFile = localFiles.length > 0;
        this.isFrom = this.isLocalFile ? 'De gegevens zijn afkomstig van jouw Local File' : 'De gegevens zijn afkomstig van onze server';
        // console.time('myTimer');
        const selectedDateIndex = this.dayNames.indexOf(this.elements.sDate.value);
        await this.filterGeojsonFeatures(hour, selectedDateIndex, this.elements.property.value, selectedRegio, selectedGemeente, selectedStName, this.isLocalFile); // console.timeEnd('myTimer');
        this.updateMapSourceAndLayer(map, this.geojson);
      } catch (error) {
        console.error(error);
      }
    },


    async filterGeojsonFeatures(hour, selectedDateIndex, selectedProperty, selectedRegio, selectedGemeente, selectedStName, isLocalFile) {
      const now = new Date();
      now.setDate(now.getDate() - selectedDateIndex);

      const date = new Date(Date.UTC(now.getFullYear(), now.getMonth(), now.getDate(), parseInt(hour) + now.getTimezoneOffset() / 60));
      const measured_time = date.toISOString().replace('T', '%20').substring(0, 19) + '00';

      if (this.interpolationStatus === 'activate') {
        this.idw_interpolation(date);
      } else if (this.currentLayerId && this.map.getLayer(this.currentLayerId)) {
        this.map.setPaintProperty(this.currentLayerId, 'raster-opacity', 0);
      }

      const filters = {
        'property': selectedProperty,
        'station': selectedStName.length > 0 ? selectedStName : [],
        'gemeente': selectedStName.length > 0 ? [] : (selectedGemeente.length > 0 ? selectedGemeente : []),
        'regio': selectedStName.length > 0 || selectedGemeente.length > 0 ? [] : (selectedRegio.length > 0 ? selectedRegio : [])
      };

      let filteredGeojson;
      if (isLocalFile) {
        filteredGeojson = await this.loadLocalFile(isLocalFile);
      } else {
        let url1 = new URL('https://dta-samenmeten-api.azurewebsites.net/api/data/stations');
        Object.entries(filters).forEach(([key, value]) => {
          if (value && value.length > 0) url1.searchParams.append(key, value);
        });

        let url2 = new URL(`https://dta-samenmeten-api.azurewebsites.net/api/data/observations?property=${selectedProperty}&measured_time=${measured_time}`);

        // Fetch data in parallel
        let [stations, observations] = await Promise.all([this.fetchData(url1.toString()), this.fetchData(url2.toString())]);

        let observationMap = new Map();
        observations.Features.forEach(observation => {
          observationMap.set(observation.properties.station_name, observation);
        });

        filteredGeojson = {
          type: 'FeatureCollection',
          Features: stations.Features.filter(station => {
            let matchingObservation = observationMap.get(station.properties.station_name);
            if (matchingObservation) {
              matchingObservation.properties.avg_value = station.properties.avg_value;
              matchingObservation.properties.max_value = station.properties.max_value;
              matchingObservation.properties.min_value = station.properties.min_value;
              station.properties = {...matchingObservation.properties};
              station.geometry = matchingObservation.geometry;
              return true;
            }
            return false;
          })
        };
      }
      this.geojson = filteredGeojson;

      [this.regios, this.gemeentes, this.stName] = await Promise.all([this.getUniqueItems(this.geojson, 'regio'), this.getUniqueItems(this.geojson, 'Gemeente'), this.getUniqueItems(this.geojson, 'station_name')]);

      this.createCheckboxes('regio', this.regios);
      this.createCheckboxes('Gemeente', this.gemeentes);
      this.createCheckboxes('station_name', this.stName);
    },


    async handleClick() {
      if (this.geojson) {
        this.downloadGeoJSON(this.geojson);
        // this.downloadCSV(this.geojson);
        // this.downloadPDF(this.geojson);
        // Show the toast
        const toastLiveExample = this.$refs.liveToast;
        const toastBootstrap = window.bootstrap.Toast.getOrCreateInstance(toastLiveExample);
        // Update the time in the toast
        const now = new Date();
        this.timeString = now.getHours() + ':' + now.getMinutes() + ':' + now.getSeconds();
        toastBootstrap.show();
      } else {
        console.error('GeoJSON data is not yet available.');
      }
    },


    async idw_interpolation(date) {
      console.log(date)
      let bounds = [3.773675345120739, 51.64377788724585, 5.031415001585676, 52.3325109475691];
      let formattedDate = date.toISOString().substring(0, 10); // Get the date in 'YYYY-MM-DD' format
      let layerId = 'interpolatie' + formattedDate + '-' + this.elements.timeSlider.value;

      // Add the layerId to the rasterLayers set if it's not already there
      this.rasterLayers.add(layerId);

      // Set the opacity of all layers to 0, except for the current one
      this.rasterLayers.forEach(id => {
        if (this.map.getLayer(id)) {
          this.map.setPaintProperty(id, 'raster-opacity', id === layerId ? 1 : 0);
        }
      });

      this.currentLayerId = layerId;

      if (!this.map.getLayer(layerId)) {
        let url = `https://geodata.zuid-holland.nl/geoserver/milieu/wms?service=WMS&version=1.1.0&request=GetMap&layers=milieu%3Aluchtkwaliteit_db&bbox=${bounds.join(',')}&width=768&height=420&srs=EPSG%3A4326&time=2023-12-07T${this.elements.timeSlider.value}:00:00.000Z&format=image/png&transparent=true&styles=luchtkwaliteit_interpolatie_alt`;
        console.log(url)
        this.map.addSource(layerId, {
          'type': 'image',
          'url': url,
          'coordinates': [[bounds[0], bounds[3]], [bounds[2], bounds[3]], [bounds[2], bounds[1]], [bounds[0], bounds[1]]]
        });
        this.map.addLayer({'id': layerId, 'type': 'raster', 'source': layerId, 'paint': {'raster-opacity': 1}});
      }
    },



    async downloadGeoJSON(geojson) {
      let dataStr = JSON.stringify(geojson);
      let dataUri = 'data:application/json;charset=utf-8,' + encodeURIComponent(dataStr);

      let link = document.createElement('a');
      link.href = dataUri;
      link.download = 'Provincie Zuid-Holland Luchtkwaliteit.geojson';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },

    // async downloadCSV(geojson) {
    //   const CSV_HEADER = 'Station naam;Datum en tijd;Property;Regio;Gemeente;Value;Unit\n';
    //   const DEFAULT_VALUE = 'N/A';
