    //       }]
    //     },
    //     options: {
    //       animation: {
    //         onComplete: function() {
    //           canvas.toBlob(function(blob) {
    //             if (blob) {
    //               let img = new Image();
    //               img.onload = function() {
    //                 doc.addImage(img, 'PNG', 10, 10, 180, 90);
    //                 doc.save('Provincie Zuid-Holland Luchtkwaliteit - Samen Meten Dashboard.pdf');
